import React from 'react';
import {connect} from "react-redux";

const ShowHelpContent = (props) => {
    return(
        <div class="container-fluid" id="showHelpContent">
            <div dangerouslySetInnerHTML={{__html:props.appSettings.helpText}} id="helpContentText"></div>
        </div>
    )
};

const mapStateToProps = (state) => {
    return {
        appSettings:state.appSettings,
    };

};

export default connect(mapStateToProps, {})(ShowHelpContent);
